<template>
  <nav>
    <swiper>
      <swiper-slide v-for="(page, index) of pages" :key="index">
        <ul>
          <li v-for="item of page" :key="item.id">
            <a href="#">
              <span class="nav-icon-icon"
                    :style="{background: 'url('+item.imgUrl+')'}"
              >
              </span>
              <span class="icon-desc">{{item.desc}}</span>
            </a>
          </li>
        </ul>
      </swiper-slide>
    </swiper>
  </nav>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
  nav ul
    display flex
    flex-wrap wrap
    padding-top 0.1rem
    li
      width 25%
      padding-top 0.08rem
      a
        display flex
        flex-direction column
        align-items center
        span[class^="nav-icon"]
          width 1.1rem
          height 1.1rem
          background-size 1.1rem auto!important
        span.icon-desc
          width 100%
          margin-top 0.08rem
          color $darkTextColor
          text-align center
          ellipsis()
</style>
