<template>
  <div>
    <div class="search">
    <input
      class="search-input"
      type="search"
      placeholder="输入城市名或拼音"
      maxlength="20"
      v-model="keyword"
    >
    </div>
    <div class="search-content" v-show="keyword" ref="search">
      <ul>
        <li
          class="item border-bottom"
          v-for="item of list"
          :key="item.id"
          @click="handleBtnClick(item.name)"
        >
          {{item.name}}
        </li>
        <li class="item" v-show="hasNoData">没有匹配到数据</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import {mapActions} from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  methods: {
    handleBtnClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapActions(['changeCity'])
  },
  computed: {
    hasNoData () {
      return !this.list.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          for (let item of this.cities[i]) {
            if (item.spell.includes(this.keyword) || (item.name.includes(this.keyword))) {
              result.push(item)
            }
          }
        }
        this.list = result
      }, 100)
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.search, {click: true})
  },
  updated () {
    this.scroll.refresh()
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
  .search
    height 0.6rem
    padding 0 0.1rem
    background $bgColor
    .search-input
      width 100%
      height 0.5rem
      color #333
      text-align center
      border-radius 0.1rem
  .search-content
    overflow hidden
    position absolute
    z-index 1
    top 1.46rem
    left 0
    right 0
    bottom 0
    background #eee
    .item
      height 0.5rem
      padding-left 0.2rem
      line-height 0.5rem
      background #fff
      color #333
</style>
