<template>
  <div class="list" ref="wrapper">
    <div>
      <div class="area">
      <div class="title border-bottom">当前城市</div>
      <ul class="button-list">
        <li class="btn-li">
          <router-link to="/"><div class="button">{{city}}</div></router-link>
        </li>
      </ul>
    </div>
      <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <ul class="button-list">
          <li
            class="btn-li"
            v-for="item of hot"
            :key="item.id"
          >
            <div class="button" @click="handleBtnClick(item.name)">{{item.name}}</div>
          </li>
      </ul>
      </div>
      <div
        class="area"
        v-for="(item, key) of cities"
        :key="key"
        :ref="key"
      >
        <div class="title border-topbottom">{{key}}</div>
        <ul class="item-list">
          <li
            class="item border-bottom"
            v-for="item1 of item"
            :key="item1.id"
            @click="handleBtnClick(item1.name)"
          >
            {{item1.name}}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import {mapState, mapActions} from 'vuex'
export default {
  name: 'CityList',
  props: {
    hot: Array,
    cities: Object,
    letter: String
  },
  computed: {
    ...mapState(['city'])
  },
  methods: {
    handleBtnClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapActions(['changeCity'])
  },
  watch: {
    letter () {
      const element = this.$refs[this.letter][0]
      this.scroll.scrollToElement(element)
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.wrapper, {click: true})
  },
  updated () {
    this.scroll.refresh()
  }
}
</script>

<style lang="stylus" scoped>
  .border-topbottom
    &::before
      border-color #ccc
    &::after
      border-color #ccc
  .border-bottom
    &::before
      border-color #ccc
  .list
    position absolute
    top 1.46rem
    left 0
    right 0
    bottom 0
    overflow hidden
    .area
      .title
        height 0.5rem
        padding-left 0.2rem
        background #eee
        line-height 0.5rem
      .button-list
        overflow hidden
        padding 0.1rem 0.6rem 0.1rem 0.1rem
        color #333
        .btn-li
          float left
          width 33.3%
        .button
          height 0.55rem
          margin 0.1rem
          border 0.02rem solid #ccc
          text-align center
          line-height 0.55rem
          color #333
          border-radius 0.1rem
      .item-list
        .item
          height 0.7rem
          padding-left 0.2rem
          line-height 0.7rem
</style>
