<template>
  <div class="detail-header">
    <router-link tag="div" to="/" class="header-abs" v-show="showAbs">
      <span class="iconfont back-icon">&#xe624;</span>
    </router-link>
    <div
      class="header-fixed"
      v-show="!showAbs"
      :style="opacityStyle"
    >
      <router-link tag="span" to="/" class="iconfont back-icon header-icon">&#xe624;</router-link>
      <h1 class="header-title">故宫</h1>
    </div>
 </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop || window.pageYOffset
      if (top > 0) {
        this.showAbs = false
        let opacity = top / 180
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = { opacity }
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
  .detail-header
    .header-abs
      position absolute
      top 0.2rem
      left 0.2rem
      width 0.6rem
      height 0.6rem
      background rgba(0, 0, 0, .6)
      line-height 0.6rem
      text-align center
      border-radius 0.3rem
    .back-icon
      color #fff
    .header-fixed
      position fixed
      z-index 1
      top 0
      left 0
      right 0
      height 0.8rem
      background $bgColor
      text-align center
      line-height 0.8rem
      .header-icon
        position absolute
        top 0
        left 0.2rem
      .header-title
        font-size 0.32rem
        color #fff
</style>
