<template>
  <div class="wrapper">
    <div class="banner" @click="handleBannerClick">
      <img class="banner-img" :src="bannerImg" alt="">
        <div class="banner-title">{{sightName}}</div>
        <div class="banner-number">
          <span class="banner-icon iconfont">&#xe634;</span>
          <span class="banner-snumber">{{gallaryImgs.length}}</span>
        </div>
    </div>
    <fade-animation>
      <common-gallery
        :imgs="gallaryImgs"
        v-show="showGallery"
        @galleryClose="handleGalleryClose"
      >
      </common-gallery>
    </fade-animation>
  </div>
</template>

<script>
import CommonGallery from 'common/gallery/Gallery'
import fadeAnimation from 'common/fadeAnimation/fadeAnimation'
export default {
  name: 'Banner',
  props: {
    sightName: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  components: {
    CommonGallery,
    fadeAnimation
  },
  data () {
    return {
      showGallery: false
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallery = true
    },
    handleGalleryClose () {
      this.showGallery = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position relative
    overflow hidden
    height 0
    padding-bottom 55%
    .banner-img
      width 100%
    .banner-title
      position absolute
      bottom 0.4rem
      left 0.25rem
      color #fff
      font-size 0.36rem
    .banner-number
      position absolute
      bottom 0.85rem
      left 0.38rem
      width 1.2rem
      height 0.4rem
      background rgba(0, 0, 0, .5)
      color #fff
      font-size 0.24rem
      line-height 0.4rem
      text-align center
      border-radius 0.2rem
      .banner-icon
        position absolute
        top 0
        left 0.2rem
      .banner-snumber
        margin-right  -0.3rem
    &::after
      content ''
      display block
      position absolute
      left 0
      bottom 0
      width 100%
      height 0.6rem
      background linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.7))
</style>
